import { cleanup, fireEvent, render } from '@testing-library/preact';
import { expect } from 'chai';
import { Button } from '../Button';
import { TabbableModeContext } from '../../hooks/UNSAFE_useTabbableMode';

afterEach(cleanup);

describe('Test Button component', () => {
  it('render', async function () {
    const content = render(<Button label="Press Me"></Button>);
    const label = await content.findByText('Press Me');
    expect(label).not.null;
  });

  it('if both label and title exist, it should show title as tooltip', async () => {
    const content = render(<Button display="icons" label="label" title="title"></Button>);
    const button = content.container.firstElementChild;
    expect(button).to.not.be.null;
    if (button) {
      const tooltipText = button.getAttribute('title');
      expect(tooltipText).to.equal('title');
    }
  });

  it('no default tooltip', async () => {
    const content = render(<Button label="label"></Button>);
    const button = content.container.firstElementChild;
    expect(button).to.not.be.null;
    if (button) {
      const tooltipText = button.getAttribute('title');
      expect(tooltipText).to.equal(null);
    }
  });

  it('if accessibleLabel exist, it should show as tooltip', async () => {
    const content = render(
      <Button display="icons" accessibleLabel="alabel" label="button"></Button>
    );
    const button = content.container.firstElementChild;
    expect(button).to.not.be.null;
    if (button) {
      const tooltipText = button.getAttribute('title');
      expect(tooltipText).to.equal('alabel');
    }
  });

  it('click', async function () {
    let count = 0;
    const increment = () => {
      count++;
    };
    const content = render(
      <div>
        <Button label="Press Me" onAction={increment}></Button>
        <span>{count}</span>
      </div>
    );
    const button = await content.findByText('Press Me');
    expect(button).not.null;
    fireEvent.click(button);
    expect(count).to.equal(1);
  });

  it('ENTER', async function () {
    let count = 0;
    const increment = () => {
      count++;
    };
    const content = render(
      <div>
        <Button label="Press Me" onAction={increment}></Button>
        <span>{count}</span>
      </div>
    );
    const button = await content.findByText('Press Me');
    expect(button).not.null;
    fireEvent.keyDown(button, { code: 'Enter', key: 'Enter', keyCode: 13, charCode: 13 });
    fireEvent.keyUp(button, { code: 'Enter', key: 'Enter', keyCode: 13, charCode: 13 });
    expect(count).to.equal(1);
  });

  it('Space', async function () {
    let count = 0;
    const increment = () => {
      count++;
    };
    const content = render(
      <div>
        <Button label="Press Me" onAction={increment}></Button>
        <span>{count}</span>
      </div>
    );
    const button = await content.findByText('Press Me');
    expect(button).not.null;
    fireEvent.keyDown(button, { code: 'Space', key: 'Space', keyCode: 32, charcode: 32 });
    fireEvent.keyUp(button, { code: 'Space', key: 'Space', keyCode: 32, charcode: 32 });
    expect(count).to.equal(1);
  });

  it('variant borderless', async () => {
    const content = render(<Button variant="borderless" label="button"></Button>);
    const button = content.container.querySelector('.oj-c-base-button-borderless');
    expect(button, 'borderless button found').not.null;
  });

  it('variant callToAction', async () => {
    const content = render(<Button variant="callToAction" label="button"></Button>);
    const button = content.container.querySelector('.oj-c-base-button-callToAction');
    expect(button, 'callToAction button found').not.null;
  });

  it('variant danger', async () => {
    const content = render(<Button variant="danger" label="button"></Button>);
    const button = content.container.querySelector('.oj-c-base-button-danger');
    expect(button, 'danger button found').not.null;
  });

  it('variant outlined', async () => {
    const content = render(<Button variant="outlined" label="button"></Button>);
    const button = content.container.querySelector('.oj-c-base-button-outlined');
    expect(button, 'outlined button found').not.null;
  });

  it('variant solid', async () => {
    const content = render(<Button variant="solid" label="button"></Button>);
    const button = content.container.querySelector('.oj-c-base-button-solid');
    expect(button, 'solid button found').not.null;
  });

  it('variant ghost', async () => {
    const content = render(<Button variant="ghost" label="button"></Button>);
    const button = content.container.querySelector('.oj-c-base-button-ghost');
    expect(button, 'ghost button found').not.null;
  });

  it('tabindex default ', async () => {
    const content = render(<Button label="button"></Button>);
    const button = content.container.firstElementChild;
    if (button) {
      const tabindex = button.getAttribute('tabindex');
      expect(tabindex).to.equal('0');
    }
  });

  it('size default ', async () => {
    const content = render(<Button label="button"></Button>);
    const button = content.container.querySelector('.oj-c-base-button-md');
    expect(button, 'default size button found').not.null;
  });

  it('size sm ', async () => {
    const content = render(<Button label="button" size="sm"></Button>);
    const button = content.container.querySelector('.oj-c-base-button-sm');
    expect(button, 'sm size button found').not.null;
  });

  it('size md ', async () => {
    const content = render(<Button label="button" size="md"></Button>);
    const button = content.container.querySelector('.oj-c-base-button-md');
    expect(button, 'md size button found').not.null;
  });

  it('size lg ', async () => {
    const content = render(<Button label="button" size="lg"></Button>);
    const button = content.container.querySelector('.oj-c-base-button-lg');
    expect(button, 'lg size button found').not.null;
  });

  it('disabled default', async () => {
    const content = render(<Button label="button"></Button>);
    const button = content.container.firstElementChild;
    if (button) {
      const disabled = button.getAttribute('disabled');
      expect(disabled).null;
    }
  });

  it('disabled', async () => {
    const content = render(<Button label="button" isDisabled={true}></Button>);
    const button = content.container.firstElementChild;
    if (button) {
      const disabled = button.getAttribute('disabled');
      const tabindex = button.getAttribute('tabindex');
      expect(tabindex).to.equal('-1');
      expect(disabled).not.null;
    }
  });

  it('display default', async () => {
    const content = render(
      <Button
        label="button"
        startIcon={<span class="startIcon"></span>}
        endIcon={<span class="endIcon"></span>}></Button>
    );
    const startIcon = content.container.querySelector('.startIcon');
    const endIcon = content.container.querySelector('.endIcon');
    expect(startIcon).not.null;
    expect(endIcon).not.null;
  });

  it('display default', async () => {
    const content = render(
      <Button
        label="button"
        startIcon={<span class="startIcon"></span>}
        endIcon={<span class="endIcon"></span>}></Button>
    );
    const label = await content.findByText('button');
    const startIcon = content.container.querySelector('.startIcon');
    const endIcon = content.container.querySelector('.endIcon');
    expect(label).not.null;
    expect(startIcon).not.null;
    expect(endIcon).not.null;
  });

  it('display all', async () => {
    const content = render(
      <Button
        label="button"
        display="all"
        startIcon={<span class="startIcon"></span>}
        endIcon={<span class="endIcon"></span>}></Button>
    );
    const label = await content.findByText('button');
    const startIcon = content.container.querySelector('.startIcon');
    const endIcon = content.container.querySelector('.endIcon');
    expect(label).not.null;
    expect(startIcon).not.null;
    expect(endIcon).not.null;
  });

  it('display icons', async () => {
    const content = render(
      <Button
        label="button"
        display="icons"
        startIcon={<span class="startIcon"></span>}
        endIcon={<span class="endIcon"></span>}></Button>
    );
    const label = content.findByText('button');
    const startIcon = content.container.querySelector('.startIcon');
    const endIcon = content.container.querySelector('.endIcon');
    expect(Object.keys(label).length === 0).to.be.true;
    expect(startIcon).not.null;
    expect(endIcon).not.null;
  });

  it('display text', async () => {
    const content = render(
      <Button
        label="button"
        display="label"
        startIcon={<span class="startIcon"></span>}
        endIcon={<span class="endIcon"></span>}></Button>
    );
    const label = await content.findByText('button');
    const startIcon = content.container.querySelector('.startIcon');
    const endIcon = content.container.querySelector('.endIcon');
    expect(label).not.null;
    expect(startIcon).null;
    expect(endIcon).null;
  });

  it('with TabbableModeContext isTabbable=true', async () => {
    const content = render(
      <TabbableModeContext.Provider value={{ isTabbable: true }}>
        <Button label="button" />
      </TabbableModeContext.Provider>
    );
    const button = await content.findByText('button');
    const tabindex = button.getAttribute('tabindex');
    expect(tabindex).to.equal('0');
  });

  it('with TabbableModeContext isTabbable=false', async () => {
    const content = render(
      <TabbableModeContext.Provider value={{ isTabbable: false }}>
        <Button label="button" />
      </TabbableModeContext.Provider>
    );
    const button = await content.findByText('button');
    const tabindex = button.getAttribute('tabindex');
    expect(tabindex).to.equal('-1');
  });
});
